.articles {
  padding: 130px 0;
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 20px;
    margin-bottom: 68px;
    &-content {
      max-width: 545px;
    }
  }
  &__title {
    margin: 0;
    margin-top: 22px;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(54, 34);
    line-height: 74px;
    text-transform: capitalize;
    color: #ffffff;
  }
  &__btn {
    display: inline-block;
    cursor: pointer;
    flex: 0 0 auto;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(17, 14);
    line-height: 28px;
    text-transform: capitalize;
    color: #ffffff;
    padding: 21px 34px;
    background: #57b8ff;
    border: 1px solid transparent;
    transition: 0.5s;
    &:hover {
      border: 1px solid var(--yellow-color);
      color: var(--yellow-color);
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 38px;
    grid-row-gap: 24px;
  }
  &__card {
    position: relative;
    grid-column: 3 span;
    grid-row: 1 span;
    height: 100%;
    &-item {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      height: 100%;
    }
    &-main {
      grid-column: 6 span;
      grid-row: 2 span;
      height: 100%;
    }
    &-img {
      display: block;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-content {
      padding-top: 30px;
    }
    &-data {
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 18px;
      color: var(--blue-color);
      margin-bottom: 25px;
    }
    &-descr {
      margin: 0;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 400;
      @include font-adaptive(23, 14);
      line-height: 46px;
      text-transform: capitalize;
      color: var(--light-color);
    }
  }
}
@include tablet {
  .articles__card {
    grid-column: 6 span;
    &:first-child {
      grid-column: 12 span;
      grid-row: 2 span;
    }
  }
}
@include mobile {
  .articles__header {
    flex-direction: column;
    align-items: flex-start;
  }
  .articles__cards {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }
  .articles__card {
    grid-column: 12 span;
    grid-row: 1 span;
  }
}
