.mode {
  &__container {
    display: flex;
    flex-wrap: wrap;
  }
  &__card {
    position: relative;
    flex: 1 1 auto;
    padding: 270px 50px;
    &-bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 5;
  }
  &__descr {
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(46, 35);
    line-height: 58px;
    text-align: center;
    text-transform: capitalize;
    color: var(--light-color);
  }
}
@include small-tablet {
  .mode__card {
    padding: 120px 20px;
  }
}
