.community {
  background: url(../img/community.jpg) no-repeat center;
  background-size: cover;
  padding: 120px 0 130px 0;
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 846px;
    margin: 0 auto;
  }
  &__title {
    margin: 0 auto;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(68, 40);
    line-height: 82px;
    text-align: center;
    color: var(--black-color);
    margin-bottom: 51px;
  }
  &__descr {
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(17, 13);
    line-height: 28px;
    text-align: center;
    color: var(--black-color);
    margin-bottom: 45px;
  }
  &__form {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    &-input {
      background: #ffffff;
      border: 1px solid #cccccc;
      padding: 26px 13px;
      max-width: 445px;
      width: 100%;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 700;
      @include font-adaptive(18, 14);
      line-height: 28px;
      color: #555555;
      &::placeholder {
        font-family: "Orbitron";
        font-style: normal;
        font-weight: 700;
        @include font-adaptive(18, 14);
        line-height: 28px;
        color: #555555;
      }
    }
    &-btn {
      cursor: pointer;
      border: 1px solid transparent;
      padding: 21px 21px;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 700;
      @include font-adaptive(18, 14);
      line-height: 28px;
      text-align: center;
      color: #ffffff;
      background: #57b8ff;
      transition: 0.5s;
      white-space: nowrap;
      &:hover {
        border: 1px solid var(--yellow-color);
        color: var(--yellow-color);
      }
    }
  }
}
@include mobile {
  .community__form {
    flex-direction: column;
    gap: 30px;
  }
}
