.costomize {
  background: #2a313c;
  padding: 50px 0;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 50px;
    flex-direction: row-reverse;
  }
  &__content {
    max-width: 560px;
  }
  &__title {
    margin: 0;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(52, 27);
    line-height: 74px;
    text-transform: capitalize;
    color: var(--light-color);
    margin-top: 24px;
    margin-bottom: 38px;
  }
  &__descr {
    margin: 0;
    font-family: "Orbitron";
    font-style: normal;
    font-weight: 400;
    @include font-adaptive(18, 14);
    line-height: 30px;
    color: rgba(223, 223, 223, 0.8);
    margin-bottom: 55px;
  }
  &__list {
    display: flex;
    gap: 20px;
  }
  &__item {
    cursor: pointer;
    border-radius: 20px;
    overflow: hidden;
    padding: 11px;
    flex: 1 0 25%;
    &-active {
      background: #3b3d46;
      border-radius: 20px;
      transition: 0.5s;
    }
    img {
      height: 100%;
      pointer-events: none;
      border-radius: 20px;
    }
  }
  &__img {
    display: flex;
    width: 100%;
    overflow: hidden;
    &-content {
      flex: 1 1 auto;
      height: 100%;
      img {
        border-radius: 20px;
        width: 100%;
        max-height: 500px;
      }
    }
  }
}
@include tablet {
  .costomize__container {
    flex-direction: column;
  }
}
