.footer {
  padding: 150px 0 50px 0;
  background: #222730;
  &__container {
    display: flex;
    justify-content: space-between;
    gap: 50px;
    flex-wrap: wrap;
  }
  &__content {
    display: flex;
    flex-direction: column;
  }
  &__item {
    display: flex;
    flex-direction: column;
    &-info {
      max-width: 290px;
      &__descr {
        margin: 0;
        font-family: "Orbitron";
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 25px;
        color: var(--gray-color);
        margin-top: 40px;
      }
    }
    &-title {
      margin: 0;
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 30px;
      text-transform: capitalize;
      color: var(--light-color);
      margin-bottom: 23px;
    }
    &-link {
      font-family: "Orbitron";
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
      line-height: 38px;
      text-transform: capitalize;
      color: var(--gray-color);
      transition: 0.5s;
      &:hover {
        color: var(--yellow-color);
      }
    }
  }
  &__social {
    display: flex;
    gap: 16px;
    &-item {
      cursor: pointer;
      background: #383d45;
      display: flex;
      padding: 5px;
      justify-content: center;
      align-items: center;
      width: 48px;
      height: 48px;
      transition: 0.5s;
      border: 1px solid transparent;
      &:hover {
        border: 1px solid var(--yellow-color);
      }
    }
  }
}
